<template>
	<div :class="rightFormClass">
		<div class="form-title">多角度人脸入库</div>
		<div class="form-input-div">
			<input v-model="form.name" placeholder="工号" autocomplete>
		</div>
		<div class="form-input-div">
			<div :class="selectClass" @click="handleSelect">
				<span>{{form.type}}</span>
				<img class="drop-down" src="./../images/drop-down.png">
				<div class="select">
					<div v-for="(item, index) in faceList"
					@click="handleChange(index)"
					:class="{active: form.type === item.label}"
					:key="index">{{item.label}}
					</div>
				</div>
			</div>
		</div>
		<div class="form-btn">
			<div @click="infoSave">录入信息</div>
			<div @click="infoCancel">取消录入</div>
		</div>
		<Message ref="msg"></Message>
	</div>
</template>

<script>
	import Message from "./Message";

	export default {
		name: "RightForm",
		components: {Message},
		props: {
			isShow: {
				default: false,
				type: Boolean,
			}
		},
		computed: {
			rightFormClass() {
				return {
					'right-form': true,
				}
			},
			selectClass() {
				return {
					"select-wrap": true,
					"select-wrap-hidden": !this.isSelectActive,
				}
			},
		},
		data() {
			return {
				isSelectActive: false,
				form: {
					name: '',
					type: '正面',
				},

				faceList: [
					{
						label: '正面',
						value: 1,
					},
					{
						label: '侧面',
						value: 2,
					},
					{
						label: '仰头',
						value: 3,
					},
					{
						label: '低头',
						value: 4,
					},
				],
			}
		},
		methods: {
			handleSelect() {
				this.isSelectActive = !this.isSelectActive;
			},
			handleChange(idx) {
				this.form.type = this.faceList[idx].label
			},
			infoSave() {
				// this.$refs.msg.showMsg({
				// 	content: "保存成功"
				// })
				window.console.log(this.form)
				this.$socket.emit('add_new', {
					P1: this.form.name,
					P2: this.form.type
				})
			},
			infoCancel() {
				this.$emit('hiddenFn')
			},
		},
		sockets: {
			add_new_response(res) {
				let obj = res
				window.console.log(obj.app_data.message, obj.app_status)
				alert(obj.app_data.message)
				if(obj.app_status == 1) {
					// this.form.name = ''
				}
				this.$emit('infoSaveSuccess', obj.app_status)
			}
		}
	}
</script>

