<template>
  <div :class="className">
    <video :class="staticStartClass"
           ref="staticStart"
           muted
           src="./video/static-start.mp4"></video>
    <video :class="birthdayClass"
           ref="birthday"
           muted
           src="./video/birthday.mp4"></video>
    <video :class="staticCircleClass"
           ref="staticCircle"
           muted
           loop
           src="./video/static-circle.mp4"></video>
  </div>
</template>

<script>
  import store from '@/store'
  export default {
    name: 'staticStart',
    data(){
      return {
        isShow: true,
        store: store.state,
        videoName: 'staticStart',
      }
    },
    watch: {
      store: {
        handler(store){
          this.isShow = ["STATIC", "COMING", "BIRTHDAY", "SHOW_NAME"].includes(store.step);
          switch (store.step) {
            case "STATIC":
            case "SHOW_NAME": this.videoName = "staticCircle";break;
            case "COMING": this.videoName = "staticStart";break;
            case "BIRTHDAY": this.videoName = "birthday";break;
            default: this.videoName = "staticCircle";
          }
          if(this.$refs[this.videoName]) this.$refs[this.videoName].play()
        },
        immediate: true,
        deep: true
      },
    },
    mounted(){
      this.$refs[this.videoName].play()
    },
    computed: {
      className: function(){
        return {
          "movie-full-scan": 1,
          "movie-full-scan-hidden": !this.isShow,
        }
      },
      staticStartClass(){
        return {
          "video": true,
          "video-hidden": this.videoName !== 'staticStart',
        }
      },
      staticCircleClass(){
        return {
          "video": true,
          "video-hidden": this.videoName !== 'staticCircle',
        }
      },
      birthdayClass(){
        return {
          "video": true,
          "video-hidden": this.videoName !== 'birthday',
        }
      },
    }
  }
</script>

<style scoped lang="less">
.movie-full-scan {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  .video {
    position: absolute;
    left: 0;
    top: 0;
    opacity: 1;
    transition: opacity 500ms ease-in-out;
    width: 100%;
  }
  .video-hidden {
    opacity: 0;
  }
  opacity: 1;
  transition: opacity .1s ease-in-out;
}
.movie-full-scan-hidden {
  opacity: 0;
}
</style>
