
const tools = {};

const dayTime = [0, 6, 9, 12, 14, 17, 19 ,22, 23];
const dayText = [
	{
		cn: "凌晨好",
		en: "Good Morning",
	},
	{
		cn: "早上好",
		en: "Good Morning",
	},
	{
		cn: "上午好",
		en: "Good Morning",
	},
	{
		cn: "中午好",
		en: "Good Noon",
	},
	{
		cn: "下午好",
		en: "Good Afternoon",
	},
	{
		cn: "傍晚好",
		en: "Good Evening",
	},
	{
		cn: "晚上好",
		en: "Good Evening",
	},
	{
		cn: "夜里好",
		en: "Good Evening",
	},];

tools.getTimePart = function(date) {
	let hour = date.getHours()
	for (let i = 0; i < dayTime.length - 1; i++) {
		if(dayTime[i] <= hour && dayTime[i+1] > hour) {
			return dayText[i];
		}
	}
};

tools.arrayBufferToBase64 = function ( buffer ) {
	let binary = '';
	let bytes = new Uint8Array(buffer);
	let len = bytes.byteLength;
	for (let i = 0; i < len; i++) {
		binary += String.fromCharCode(bytes[i]);
	}
	return window.btoa( binary );
}

export default tools;
