<script>
	import CenterTextHello from "../components/CenterTextHello";
	import RightForm from "../components/RightForm";
	import store from '@/store'
	import tools from '@/utils/tools'


	const videoSize = [8, 6.3];
	const processor = {
		doLoad(videoEle, canvasEle) {
			this.video = videoEle;
			this.c1 = canvasEle;
			this.ctx1 = this.c1.getContext('2d');
			let self = this;
			this.video.addEventListener('play', function() {
				self.width = self.video.videoWidth;
				self.height = self.video.videoHeight;
				// self.c1.width = self.width
				// self.c1.height = self.height
				self.timerCallback();
			}, false);
		},
		timerCallback() {
			if (this.video.paused || this.video.ended) {
				return;
			}
			this.computeFrame();
			let self = this;
			setTimeout(function() {
				self.timerCallback();
			}, 0);
		},
		computeFrame() {
			let base = +document.documentElement.style.fontSize.replace("px", "");
			this.ctx1.drawImage(this.video, 0, 0, videoSize[0]*base, videoSize[1]*base);
			// this.ctx1.drawImage(this.video, 0, 0, this.width, this.height);
			// let frame = this.ctx1.getImageData(0, 0, this.width, this.height);
			// let l = frame.data.length / 4;
			//
			// for (let i = 0; i < l; i++) {
			// 	let r = frame.data[i * 4 + 0];
			// 	let g = frame.data[i * 4 + 1];
			// 	let b = frame.data[i * 4 + 2];
			// 	if (g > 100 && r > 100 && b < 43)
			// 		frame.data[i * 4 + 3] = 0;
			// }
			// this.ctx2.putImageData(frame, 0, 0);
			return;
		}
	};

	export default {
		name: "Scan",
		components: {RightForm, CenterTextHello},
		props: {},
		data() {
			return {
				processor: processor,
				paused: false,
				store: store.state,
				isShow: false,
				scanMain: false,
				scanIsMoving: false,
				triangleBgPosition: "",
				rightBottomStyles: {},
				scanTime: undefined,
				resizeTime: undefined,
				imgData: undefined,
			}
		},
		watch: {
			store: {
				handler(store) {
					this.scanMain = store.step === "SHOW_NAME_SCAN";
					if (this.scanMain) {
						this.startVideo();
						this.isShow = true
						// this.$refs.video.play();
					} else {
						// this.$refs.video.pause();
					}
				},
				deep: true
			},
		},
		computed: {
			scanMainClass() {
				return {
					"scan-main": true,
					"scan-main-hidden": !this.scanMain,
				}
			},
			formInputClass: function () {
				return {
					'form-input': true,
					'form-input-right': this.isShow,
				}
			},
			centerTextHelloClass: function () {
				return {
					'center-text-wrap': true,
					'center-text-wrap-hidden': this.isShow,
				}
			},
			centerFormClass: function () {
				return {
					'center-form-hidden': true,
					'center-form-show': this.isShow,
				}
			},
			scanVideoClass: function () {
				return {
					'scan-video': true,
					'scan-video-hidden': !this.isShow,
				}
			},
			movingScanClass: function() {
				return {
					"moving-scan": true,
					"moving-scan-bottom": this.scanIsMoving,
				}
			}
		},
		created() {
			this.resizeTime = window.addEventListener("resize", () => {
				this.calcTriangle()
			})
		},
		mounted() {
			this.calcTriangle();
			// this.processor.doLoad(this.$refs.video, this.$refs.canvas);
		},
		methods: {
			startVideo() {
				this.paused = false
