				// let constraints = {
				// 	video: true,
				// 	audio: false
				// };
				// let _self = this;
				// navigator.mediaDevices.getUserMedia(constraints).then((mediaStream) => {
				// 	let video = this.$refs.video
				// 	video.srcObject = mediaStream
				// 	video.onloadedmetadata = function () {
				// 		video.play();
				// 		_self.calcTriangle()
				// 	};
				// })
			},
			cancelScan() {
				this.isShow = false
				window.setTimeout(() => {
					store.state.step = 'STATIC'
				}, 500)
			},
			infoSaveSuccess (status) {
				if(status == '1' || status == '2' || status == '3') this.startVideo()
			},
			calcTriangle() {
				// let base = +document.documentElement.style.fontSize.replace("px", "");

				// let width = document.documentElement.clientWidth + "px";
				// this.triangleBgPosition = width + ' auto';
				// // 862.385px 343.802px
				// let right = this.$refs.video.offsetWidth + 2 * base + 'px';
				// let top = this.$refs.video.offsetHeight - (2.3 + 1) * base + 'px';
				// this.rightBottomStyles = {
				// 	backgroundSize: width + ' auto',
				// 	backgroundPosition: right + " " + top
				// };

				// video
				// this.$refs.canvas.width = base*videoSize[0];
				// this.$refs.canvas.height = base*videoSize[1];
			},

			handleClick(){
				// if(this.$refs.video.paused) {
				window.console.log(this.paused)
				if(this.paused) {
					this.reCaptureImg()
				}else {
					this.captureImg()
				}
			},

			drawVideo(val) {
				// let dom = this.$refs.canvas
				// let ctx = dom.getContext('2d')
				// let width = val[0].length
				// let height = val.length
				// dom.width = width
				// dom.height = height
				// window.console.log(width, height)
				// let imgData = ctx.createImageData(width, height);
				// val.forEach((v1, i1) => {
				// 	v1.forEach((v2, i2) => {
				// 		imgData.data[i1 * width * 4 + i2 * 4] = v2[2]
				// 		imgData.data[i1 * width * 4 + i2 * 4 + 1] = v2[1]
				// 		imgData.data[i1 * width * 4 + i2 * 4 + 2] = v2[0]
				// 		imgData.data[i1 * width * 4 + i2 * 4 + 3] = 255
				// 	})
				// })
				// ctx.putImageData(imgData, 0, 0);

				// ctx.drawImage(val, 0, 0);

				this.imgData = 'data:image/jpg;base64,' + tools.arrayBufferToBase64(val)
			},

			captureImg(){
				this.scanIsMoving = true;
				this.scanTime = window.setTimeout(() => {
					window.clearTimeout(this.scanTime)
					this.scanIsMoving = false;
					this.$socket.emit('lock_video', {data: 'I\'m connected!'})
					// this.$refs.video.pause();
				}, 1000)
			},
			reCaptureImg(){
				this.scanTime && window.clearTimeout(this.scanTime)
				this.scanIsMoving = false;
				// this.$refs.video.play();
				this.startVideo()
			},
		},
		destroyed() {
		},
		sockets: {
			get_video_response(res) {
				if(!this.paused) {
					let obj = res
					window.console.log(obj.app_data.message, obj.app_status)
					if(obj.app_status == 1) {
						this.drawVideo(obj.app_data.video_pic)
					}
				}
			},
			lock_video_response(res) {
				let obj = res
				window.console.log(obj.app_data.message, obj.app_status)
				if(obj.app_status == 1) {
					this.drawVideo(obj.app_data.video_pic)
					this.paused = true
					alert("拍照有效，点击图像可重新拍照")
				} else {
					alert(obj.app_data.message)
					this.startVideo()
				}
			}
		}
	}
</script>

