<template>
	<div :class="centerTextClass">
		<div class="hello-cn-1">{{timePart.cn}}</div>
		<div class="hello-en-1">{{timePart.en}}</div>
		<div class="hello-cn-2">欢迎来到凡普金科</div>
		<div class="hello-en-1">Welcome to Finup</div>
	</div>
</template>

<script>
	import tools from './../utils/tools'

	export default {
		name: "CenterTextHello",
		props: {
			isShow: {
				default: false,
				type: Boolean,
			}
		},
		computed: {
			centerTextClass() {
				return {
					'center-text': true,
					'center-text-hidden': !this.isShow,
				}
			},
		},
		data(){
			return {
				timePart: "",
				time1: undefined,
			}
		},
		methods: {
			initTime() {
				this.timePart = tools.getTimePart(new Date());
			}
		},
		created() {
			this.initTime()
			this.time1 = window.setInterval(() => {
				this.initTime()
			}, 1000*60)
		},
		destroyed() {
			if(this.time1) {
				window.clearInterval(this.time1)
			}
		}
	}
</script>

<style scoped lang="less">
	.center-text {
		color: white;
		font-weight: bold;
		opacity: 1;

		.hello-cn-1 {
			font-size: 1rem;
			line-height: 1.2rem;
		}

		.hello-cn-2 {
			margin-top: .45rem;
			font-size: .7rem;
			line-height: 1.05rem;
		}

		.hello-en-1 {
			line-height: .48rem;
			font-size: .32rem;
			color: #1BF0FD;
		}

	}

	.center-text-hidden {
		opacity: 0;
	}
</style>
