const stepStatus = [
	"STATIC", //静默状态、没有人
	"COMING", // 来人了，有没有人过生日都会显示
	"BIRTHDAY", // 有人过生日
	"SHOW_NAME", // 没人过生日，挨个显示经过的人
	"SHOW_NAME_SCAN", // 显示经过的人-> 录入
	"BIRTHDAY_SCAN", // 有人过生日-> 录入
];

export default {
	state: {
		step: stepStatus[0],
		startScan: false,
		homeHidden: false,
		videoBg: false,
		isShow: false,
		birthdayTime: undefined,
	},
	keepStatic() {
		this.state.step = stepStatus[0];
	},
	showNameScan() {
		this.state.step = stepStatus[4];
	},
	changeStepState(step) {
		this.state.step = step;

		if (step === "BIRTHDAY") {
			this.state.birthdayTime = window.setTimeout(() => {
				this.state.step = "STATIC"
			}, 5000);
		} else {
			this.state.birthdayTime && window.clearTimeout(this.state.birthdayTime)
		}
	},
};
