<template>
	<div :class="birthdayTextClass">
		<div class="text-cn">{{user.nameCn}}生日快乐</div>
		<div class="text-en">{{user.nameEn}} Happy birthday</div>
	</div>
</template>

<script>
	import store from '@/store'

	export default {
		name: "birthday",
		props: {
			user: {
				type: Object,
				default(){return {}},
			},
		},
		data() {
			return {
				store: store.state,
				birthdayText: false,
			}
		},
		watch: {
			store: {
				handler(store) {
					this.birthdayText = store.step === "BIRTHDAY"
					// if(this.birthdayText) this.keepTime()
				},
				deep: true,
				immediate: true,
			},
		},
		computed: {
			birthdayTextClass: function () {
				return {
					"birthday-text": true,
					'birthday-text-hidden': !this.birthdayText,
				}
			},
		},
		methods: {
			keepTime() {
				window.setTimeout(() => {
					store.state.step = 'STATIC'
				}, 5000)
			},
		},
	}
</script>

<style scoped lang="less">
	.birthday-text {
		position: absolute;
		top: 2rem;
		width: 100%;
		font-weight: bold;
		text-align: center;
		opacity: 1;
		transition: all ease-in-out .3s;

		.text-cn {
			font-size: .7rem;
			color: white;
		}

		.text-en {
			font-size: .32rem;
			color: #1BF0FD;
		}
	}

	.birthday-text-hidden {
		top: 2.5rem;
		opacity: 0;
	}
</style>
