<template>
	<div :class="homeClass">
		<CenterTextHello class="center-text-wrap" :is-show="!startScan"></CenterTextHello>
		<Birthday :user="birthEmployee"></Birthday>
		<div v-for="(item,index) in employeeList" :key="index" :class="employeeClass + (item.id ? '' : ' employee-none ') + (item && item.class)">
			<!--			<IconAndHeader :name="item.iconAndHeaderName[0]"></IconAndHeader>-->
			<CanvasHeader v-if="item.class == 'employee-left-top' || item.class == 'employee-left-bottom'"
										:image-data="item.imgData"></CanvasHeader>
			<IconAndHeader v-else :name="item.iconName"></IconAndHeader>
			<div class="name">
				<div class="name-cn">{{item.nameCn}}</div>
				<div class="name-en">{{item.nameEn}}</div>
			</div>
			<CanvasHeader v-if="item.class == 'employee-right-top' || item.class == 'employee-right-bottom'"
										:image-data="item.imgData"></CanvasHeader>
			<IconAndHeader v-else :name="item.iconName"></IconAndHeader>
		</div>
		<div :class="footerClass">
			<!--			<button @click="comingFn">来人了</button>-->
			<!--			<button @click="birthdayFn">生日了</button>-->
			<span class="blue-font">*</span>请<span class="blue-font">未能识别</span>或<span class="blue-font">识别错误</span>的同学单独留在取景框内，以便录入准确信息
			<div @click="scanIn" class="btn">录入信息</div>
		</div>
	</div>
