<script>
	// @ is an alias to /src
	import IconAndHeader from '@/components/IconAndHeader.vue'
	import CenterTextHello from "../components/CenterTextHello";
	import store from '@/store'
	import Birthday from "./Birthday";

	// import getNameData from './data'
	import CanvasHeader from "../components/CanvasHeader";

	// let testData = [
	// 	{
	// 		class: "employee-left-top",
	// 		nameCn: '吴镇乐',
	// 		nameEn: 'Zhenle Wu',
	// 		iconAndHeaderName: ['/images/employee-header.png', 'iconCenterLeft'],
	// 	},
	// 	{
	// 		class: "employee-right-top",
	// 		nameCn: '吴镇乐',
	// 		nameEn: 'Zhenle Wu',
	// 		iconAndHeaderName: ['iconCenterRight', 'iconCenterNone'],
	// 	},
	// 	{
	// 		class: "employee-left-bottom",
	// 		nameCn: '吴镇乐',
	// 		nameEn: 'Zhenle Wu',
	// 		iconAndHeaderName: ['/images/employee-header.png', 'iconCenterLeft'],
	// 	},
	// 	{
	// 		class: "employee-right-bottom",
	// 		nameCn: '吴镇乐',
	// 		nameEn: 'Zhenle Wu',
	// 		iconAndHeaderName: ['iconCenterRight', 'iconCenterNone'],
	// 	},
	// ];
	// testData = []

	const classIcon = ["employee-left-top", "employee-right-top", "employee-left-bottom", "employee-right-bottom"]

	export default {
		name: 'home',
		components: {
			CanvasHeader,
			Birthday,
			CenterTextHello,
			IconAndHeader,
		},
		data() {
			return {
				store: store.state,
				employeeList: [],
				birthEmployee: {},
				startScan: false,
				footer: false,
				homeHidden: false,
				birthdayText: false,
				isGun: false,
			}
		},
		watch: {
			store: {
				handler(store) {
					this.startScan = ["SHOW_NAME_SCAN", "BIRTHDAY_SCAN", "BIRTHDAY"].includes(store.step)
					this.footer = ["STATIC", "COMING", "SHOW_NAME", "BIRTHDAY"].includes(store.step)
					this.homeHidden = store.step !== "SHOW_NAME"
					this.birthdayText = store.step === "BIRTHDAY"
					if (store.step === "STATIC") {
						this.loadData()
					}
					this.isGun = !["STATIC", "COMING", "SHOW_NAME"].includes(store.step)
				},
				deep: true,
				immediate: true,
			},
		},
		created() {
			// this.loadData();
		},
		computed: {
			homeClass() {
				return {
					home: true,
					"home-hidden": this.homeHidden,
				}
			},
			employeeClass() {
				return 'employee ' + (this.startScan ? 'employee-hidden ' : '');
			},
			centerTextClass() {
				return {
					'center-text': true,
					'center-text-hidden': this.startScan,
				}
			},
			footerClass: function () {
				return {
					footer: true,
					'footer-down': !this.footer,
				}
			}
		},
		methods: {
			loadData() {
				this.$socket.emit('get_name', {data: 'I\'m connected!'})
			},
			firstEmployee(next) {
				store.changeStepState("COMING")
				let time2 = window.setTimeout(() => {
					if (!this.isGun) {
						store.changeStepState(next) //BIRTHDAY,SHOW_NAME
					}
					window.clearTimeout(time2)
				}, 3000)
			},
			scanIn() {
				let time = store.changeStepState("SHOW_NAME_SCAN")
				window.setTimeout(() => {
					window.clearTimeout(time)
					this.employeeList = []
				}, 1000)
			},
			comingFn(getNameData) {
				if (this.employeeList.length === 0 && getNameData.app_data.persons.length > 0) {
					this.firstEmployee("SHOW_NAME")
				}
				if (getNameData.app_data.persons) {
					// let pp = Object.keys(getNameData.app_data).length - 1;
					let time = window.setTimeout(() => {
						window.clearTimeout(time)

						let getP = getNameData.app_data.persons.map(v1 => ({
							id: v1.p1_id,
							nameCn: v1.c_name,
							nameEn: v1.e_name,
							isBirth: v1.is_birth,
							imgData: v1.crop_img
						})) //.sort((a, b) => a.id - b.id);
						// this.employeeList.forEach((v1, i1) => {
						// 	let idx = getP.findIndex(v2 => v1 && v2 === v1.id)
						// 	if(idx >= 0 && idx !== i1) {
						// 		[getP[i1], getP[idx]] = [getP[idx], getP[i1]]
						// 	}
						// })

						let noneBirth = true;
						this.employeeList = getP.map((v, i) => {
							if(v && v.id) {
								v.class = classIcon[i]
								v.iconName = classIcon[i]
								if(noneBirth && (+v.isBirth)) {
									noneBirth = false
									this.birthEmployee = this.employeeList[i]
									this.birthdayFn()
								}
								return v;
							} else {
								return {};
							}
						})

						// for (let i = 0; i < pp; i++) {
						// 	let data = getNameData.app_data[`P_${i + 1}`];
						// 	this.$set(this.employeeList, i, {
						// 		class: classIcon[i],
						// 		iconName: classIcon[i],
						// 		id: data.p1_id,
						// 		nameCn: data.c_name,
						// 		nameEn: data.e_name,
						// 		isBirth: data.is_birth,
						// 		imgData: data.crop_img
						// 	})
						// 	window.console.log(data.is_birth)
						// 	if ((+data.is_birth)) {
						// 		this.birthEmployee = this.employeeList[i]
						// 		this.birthdayFn()
						// 	}
						// 	window.console.log(i, data.c_name)
						// }
					}, 100)
				}

				// new Array(3).fill(1).forEach((v, i) => {
				//   window.setTimeout(() => {
				//     this.employeeList.push(testData[i])
				//   }, 1000*(i+1))
				// })
			},
			birthdayFn() {
				if (this.employeeList.length > 0) {
					store.changeStepState("BIRTHDAY")
				} else {
					this.firstEmployee("BIRTHDAY")
				}
				this.employeeList = []
			},
		},
		sockets: {
			connect() {
				window.console.log('socket connected');
			},
			get_name_response(res) {
				let obj = res
				if ((!this.isGun)) {
					// if (obj.app_status == 0) window.console.log(obj.app_data.message)
					// else this.comingFn(obj)
					this.comingFn(obj)
				}
			}
		}
	}
</script>

