<template>
  <div id="app">
    <div class="header">
      数据中心欢迎系统
    </div>
    <StaticStart :is-show="videoBg"></StaticStart>
    <Home ref="homeText"></Home>
    <Scan ref="scan" @stopScan="stopScan"></Scan>
  </div>
</template>

<script>
  import StaticStart from './views/StaticStart'
  import Home from './views/Home'
  import Scan from "./views/Scan";
  import store from '@/store'


  export default {
    name: 'app',
    components: {
      Scan,
      StaticStart,
      Home,
    },
    data(){
      return {
        videoBg: store.state.step === "STATIC",
      }
    },
    mounted() {
      this.initFontSize()
      this.resizeHtmlFontSize()
    },
    methods: {
      startScan(){
        this.$refs.scan.startScan(!this.videoBg)
        this.videoBg = !this.videoBg;
      },
      stopScan(){
        let _self = this;
        window.setTimeout(()=> {
          this.videoBg = !this.videoBg;
          _self.$refs.homeText.changeState(false)
        }, 600)
      },
      initFontSize(){
        let _self = this;
        window.addEventListener('resize', function() {
          _self.resizeHtmlFontSize();
        })
      },
      resizeHtmlFontSize(){
        let designSize = 1920; // 设计图尺寸
        let html = document.documentElement;

        let wW = html.clientWidth;// 窗口宽度
        let rem = wW * 100 / designSize;

        document.documentElement.style.fontSize = rem + 'px';
        document.documentElement.style.height = wW*1080/1920 + 'px'
      }
    }
  }
</script>

